import type { MarkdownToHTMLRule } from '@ali/4ever-cangjie';

const rule: MarkdownToHTMLRule = {
  uses: [{
    plugin: () => import('markdown-it-texmath'),
    data: [{
      engine: {
        renderToString: (text: string, options: { displayMode: boolean }) => {
          if (options.displayMode) {
            return `<span type="formula" value="${text.trim()}" mode="block"></span>`;
          } else {
            return `<span type="formula" value="${text.trim()}" mode="inline"></span>`;
          }
        }
      }
    }]
  }]
};

export default rule;
